import { domUtils } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export default function getAllRowsDomRect(_, { node }: { node: Table }) {
  const table = domUtils.findDOMNodeSafely(node.key) as HTMLTableElement;

  if (!table) return [];

  return Array.from(table.querySelectorAll('tr')).map((tableRow) => {
    return tableRow.getBoundingClientRect();
  });
}
